/* stylelint-disable */
.slot {
  padding: 50px 0;
  background: #0b764f url("../img/home/slot/bg-desc.webp") no-repeat 50% 0/cover;
}
.slot__container {
  display: flex;
  flex-direction: column;
}
.slot__title {
  margin: 0 0 10px 0;
  color: #ffffff;
  text-shadow: 0px 2px 1px rgba(0, 0, 0, 0.25);
  text-align: center;
}
.slot__text {
  font-size: 15px;
  line-height: 22px;
  color: #d2e4dd;
  text-shadow: 0px 2px 1px rgba(0, 0, 0, 0.25);
  text-align: center;
  max-width: 940px;
  margin: 0 auto 20px auto;
}
.slot__link {
  font-family: "Popins-semb", sans-serif;
  font-size: 17px;
  line-height: 20px;
  letter-spacing: 0.02em;
  align-self: center;
  text-align: center;
  padding: 19px 85px 17px;
}
.slot__list {
  display: grid;
  grid-template-columns: auto auto auto auto;
  gap: 25px;
  margin: 0 0 30px 0;
}
.slot__item {
  box-shadow: 0px 2px 2px 0px rgba(0, 0, 0, 0.1);
  display: flex;
  flex-direction: column;
  padding: 20px;
  border-radius: 12px;
  background: #ffffff;
  position: relative;
  max-width: 330px;
}
.slot__item-info {
  position: absolute;
  cursor: pointer;
  top: 20px;
  right: 20px;
}
.slot__item-info-text {
  display: none;
  padding: 15px;
  border-radius: 8px;
  background: linear-gradient(270deg, #0b764f 0%, #0b764f 100%);
  color: #ffffff;
  width: 150px;
  position: absolute;
  top: 35px;
  right: 0;
  font-size: 12px;
  line-height: 15px;
}
.slot__item-info-text._show {
  display: block;
}
.slot__item-image {
  max-width: 160px;
  margin: 0 0 10px 0;
}
.slot__item-image img {
  border-radius: 8px;
}
.slot__item-title {
  flex: 1 1 auto;
  font-size: 17px;
  line-height: 26px;
  letter-spacing: -0.004em;
  color: #151a18;
  margin: 0 0 2px 0;
}
.slot__item-licensed {
  display: flex;
  color: #7387ba;
  font-size: 12px;
  line-height: 15px;
  letter-spacing: -0.004em;
  margin: 0 0 20px 0;
}
.slot__item-licensed-one {
  margin: 0 8px 0 0;
}
.slot__item-link {
  align-self: flex-start;
  display: inline-block;
  min-width: 160px;
  text-align: center;
  padding: 12px 30px 10px;
  font-size: 15px;
  line-height: 20px;
}

@media screen and (max-width: 1240px) {
  .slot__container {
    padding: 0;
  }
  .slot__title, .slot__text {
    padding: 0 15px;
  }
  .slot__list {
    display: flex;
    overflow-x: scroll;
    align-items: flex-start;
    gap: 0;
    padding: 0 15px;
  }
  .slot__item {
    min-width: 310px;
    max-width: 330px;
  }
  .slot__item:not(:last-child) {
    margin: 0 15px 0 0;
  }
}
@media screen and (max-width: 600px) {
  .slot {
    padding: 30px 0;
    background: linear-gradient(180deg, #085a3d 0%, #038941 100%);
  }
  .slot__text {
    font-size: 13px;
    line-height: 22px;
  }
  .slot__link {
    width: calc(100% - 30px);
  }
}
.faq {
  padding: 50px 0;
  background: #ffffff;
  background-size: 100%;
}
.faq__title {
  text-align: center;
  color: #151a18;
  margin: 0 0 25px 0;
}

.accordion__item:not(:last-child) {
  margin: 0 0 10px 0;
}

.accordion__header {
  display: flex;
  position: relative;
  align-items: center;
  font-family: "Popins-semb", sans-serif;
  padding: 22px 55px 21px 20px;
  background: #f3f5f6;
  border-radius: 8px;
  font-size: 18px;
  line-height: 27px;
  color: #151a18;
  cursor: pointer;
  transition: background-color 0.3s ease;
}
.accordion__header::after {
  display: inline-block;
  position: absolute;
  content: "";
  right: 30px;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 0px 7px 9px;
  border-color: transparent transparent #858585 transparent;
  transform: rotate(180deg);
  margin: 0 0 0 6px;
  transition: transform 0.3s ease, border-color 0.3s ease;
}
.accordion__header::before {
  content: "";
  width: 28px;
  height: 28px;
  min-width: 28px;
  min-height: 28px;
  margin: 0 10px 0 0;
  background: url("../img/general-block-image/faq-image.svg") no-repeat center/contain;
  display: inline-block;
}

.accordion__item_slidedown .accordion__header,
.accordion__item_show .accordion__header {
  border: none;
}

.accordion__header:hover {
  background-color: #c8c8d0;
}

.accordion__content {
  display: flex;
  flex-direction: column;
  padding: 25px 35px 15px;
  background: transparent;
}
.accordion__content p {
  font-size: 14px;
  line-height: 26px;
  color: #858585;
}
.accordion__content p:not(:last-child) {
  margin: 0 0 20px 0;
}

.accordion__item:not(.accordion__item_show) .accordion__body {
  display: none;
}

.accordion__item_show .accordion__header::after, .accordion__item_slidedown .accordion__header::after {
  transform: rotate(0deg);
  border-color: transparent transparent #0b764f transparent;
}

@media screen and (max-width: 600px) {
  .faq {
    padding: 30px 0;
  }
  .faq__container {
    padding: 0;
  }
  .faq__title {
    margin: 0 0 20px 0;
    padding: 0 15px;
  }
  .accordion__header {
    border-radius: 0;
    padding: 15px 35px 14px 15px;
    font-size: 14px;
    line-height: 21px;
  }
  .accordion__header::before {
    width: 20px;
    height: 20px;
    min-width: 20px;
    min-height: 20px;
    margin: 0 10px 0 0;
  }
  .accordion__header::after {
    border-width: 0 5px 7px;
    right: 15px;
  }
  .accordion__content {
    padding: 15px 15px 10px 15px;
  }
  .accordion__content p {
    font-size: 12px;
    line-height: 26px;
  }
  .accordion__content p:not(:last-child) {
    margin: 0 0 15px 0;
  }
}
